@import (less) "color.less";

:host(.ng-dirty.ng-invalid.nbp-show-validation) {
  .spinner-container {
    background-color: @white;
    border: 2px solid @red;
    border-radius: 2px;
  }
}

.spinner-container {
  background-color: @white;
  border: 2px solid @darkest_gray;
  border-radius: 2px;
  overflow: auto;
  padding: 4px;
  height: 50px;
  // box-shadow: inset 1px 1px 2px 0px;
  
  &:disabled {
    background-color: @light_gray;
  }

  .spinner-button {
    width: 30px;
    height: 38px;
    background-color: @green;
    font-size: 24px;
    line-height: 24px;
    color: white;
    border: none;
    border-radius: 2px;

    &:disabled {
      background-color: @light_gray;
    }

    &.left {
      float: left;
    }

    &.right {
      float: right;
    }
  }

  .spinner-input {
    width: calc(~'100% - 64px');
    border: none;
    height: 32px;
    text-align: center;
    font-size: 15px;
    color: @darkest_gray;
    font-weight: bold;
  }
}
